import { useState } from 'react';

export const useModal = () => {
  const [open, setOpen] = useState(false);
  const [element, setElement] = useState(null);

  function onOpen(elemento) {
    setOpen(true);
    setElement(elemento)
  }

   function onClose() {
    setOpen(false);
    setElement(null)
  }

  return {
    open,
    onOpen,
    onClose,
    element
  }
};