import React, { Fragment, useState, useContext, useEffect } from "react";
import { Typography } from "@material-ui/core";
import { useModal } from "../../hooks";
import { PortfolioContext } from "../../helpers";
import Modal from "./Modal";

export function Grid({ sectionTitle, gridElements = [], gridCssClass = "" }) {
  const modalViewMore = useModal();
  const { language, labels } = useContext(PortfolioContext);
  const {
    misc: { buttonViewMore },
  } = labels || {};

  return (
    <div className={"grid-container " + gridCssClass}>
      <GridModal
        open={modalViewMore.open}
        onClose={() => modalViewMore.onClose()}
        titulo={sectionTitle}
        isFullScreen={true}
        element={modalViewMore.element}
        labels={labels}
      />
      <Typography variant="h1" align="center">
        {sectionTitle}
      </Typography>
      <div className="grid">
        {gridElements.map(
          (element, index) =>
            !element.hidden && (
              <div className={"grid-element-container"} key={index}>
                <div className="grid-element-img-container">
                  {element.banner ? (
                    <img src={element.banner} alt="logo" />
                  ) : null}
                </div>
                <div className="grid-element-text-container">
                  <div className="top-text">
                    <Typography variant="h3" align="center">
                      {element.titulo[language].toUpperCase()}
                    </Typography>
                    <Typography variant="h6" align="center">
                      {element.subtitulo[language].toUpperCase()}
                    </Typography>
                  </div>
                  <button onClick={(event) => modalViewMore.onOpen(element)}>
                    <Typography variant="body1" align="center">
                      {buttonViewMore[language].toUpperCase()}
                    </Typography>
                  </button>
                </div>
              </div>
            )
        )}
      </div>
    </div>
  );
}

function GridModal({ open, onClose, titulo, isFullScreen, element }) {
  const { language, labels } = useContext(PortfolioContext);
  const [activeIndex, setActiveIndex] = useState(0);
  const {
    misc: { linkLabels, linkIcons },
  } = labels;
  const {
    img = [],
    titulo: elementTitle,
    subtitulo,
    descripcion,
    hashtags,
    links = [],
    time,
  } = element || {};

  useEffect(() => {
    setActiveIndex(0);
  }, [open]);

  return (
    <Modal
      open={open}
      onClose={onClose}
      titulo={titulo}
      isFullScreen={isFullScreen}
    >
      {element ? (
        <div className="grid-element-modal-content-container scrollable">
          {img.length > 0 && (
            <Fragment>
              <div
                className="grid-element-modal-img-container"
                style={{
                  width: img.length * 100 + "vw",
                  left: -((activeIndex % img.length) * 100) + "%",
                }}
              >
                {img.map((image, index) => (
                  <div
                    key={"img" + index}
                    onClick={() => setActiveIndex((index + 1) % img.length)}
                  >
                    <img src={image} alt="logo" />
                  </div>
                ))}
              </div>
              {img.length > 1 && (
                <div className="grid-element-modal-img-selector-container">
                  {img.map((image, index) => (
                    <button
                      key={"img-button" + index}
                      className={
                        activeIndex === index
                          ? "modal-img-selector-button-selected"
                          : ""
                      }
                      onClick={() => setActiveIndex(index)}
                    ></button>
                  ))}
                </div>
              )}
            </Fragment>
          )}
          <div className="grid-element-modal-text-container">
            <Typography variant="h2">
              {elementTitle[language].toUpperCase()}
            </Typography>
            <Typography variant="h4">
              {subtitulo[language].toUpperCase()}
            </Typography>
            <Typography variant="body1">
              {hashtags[language].join(", ").toUpperCase()}
            </Typography>
            <Typography variant="body1">{descripcion[language]}</Typography>
            {time && <Typography variant="body1">{time[language]}</Typography>}
          </div>
          <div className="grid-element-modal-links-container">
            {links.map((linkElement, index) => {
              const { link = "", icon = null, type = "" } = linkElement;
              return (
                <div className="link" key={"lnik-icon" + index}>
                  <a href={link} target="_blank" rel="noopener noreferrer">
                    {linkIcons[type] || ""}{" "}
                    <Typography variant="body1">
                      {linkLabels[type][language] || ""}
                    </Typography>
                  </a>
                </div>
              );
            })}
          </div>
        </div>
      ) : null}
    </Modal>
  );
}
