import React, { useContext } from 'react';
import { Grid } from './Layout'
import { PortfolioContext } from '../helpers'

export function ArticulosGrid() {
    const { language, labels } = useContext(PortfolioContext);
    const { header: { sectionTitles }, medium } = labels || {}
    
    return (
        <Grid sectionTitle={sectionTitles.medium[language]} gridElements={medium} gridCssClass="articulos-container" />
    )
}