import React from "react";
import { Typography } from "@material-ui/core";
import MailOutlineOutlined from "@material-ui/icons/MailOutlineOutlined";

export function ContactBar({ isHorizontal = false, isMini = false }) {
  const onMailClick = () => {
    window.location.href = "mailto:leandroercoli@gmail.com";
  };
  const onLinkedinClick = () => {
    window.open("https://www.linkedin.com/in/leandroercoli", "_blank");
  };
  const onGithubClick = () => {
    window.open("https://github.com/leandroercoli", "_blank");
  };
  const onMediumClick = () => {
    window.open("https://medium.com/@leandroercoli", "_blank");
  };

  return (
    <div
      className={
        "contact-bar-container " +
        (isHorizontal ? "contact-bar-container-horizontal" : "")
      }
    >
      <div className="item" onClick={onMailClick}>
        <MailOutlineOutlined
          className={"icon icon-even " + (isMini ? " icon-small" : "")}
          alt="mail"
        />
        {!isMini && (
          <Typography variant="h4" className="texto">
            LEANDROERCOLI@GMAIL.COM
          </Typography>
        )}
      </div>
      <div className="item" onClick={onLinkedinClick}>
        <img
          src={"/static/img/icon-linkedin.png"}
          className={"icon icon-odd " + (isMini ? " icon-small" : "")}
          alt="linkedin"
        />
        {!isMini && (
          <Typography variant="h4" className="texto">
            /IN/LEANDROERCOLI
          </Typography>
        )}
      </div>
      <div className="item" onClick={onGithubClick}>
        <img
          src={"/static/img/icon-github.png"}
          className={"icon icon-even " + (isMini ? " icon-small" : "")}
          alt="github"
        />
        {!isMini && (
          <Typography variant="h4" className="texto">
            /LEANDROERCOLI
          </Typography>
        )}
      </div>
      <div className="item" onClick={onMediumClick}>
        <img
          src={"/static/img/icon-medium.png"}
          className={"icon icon-odd " + (isMini ? " icon-small" : "")}
          alt="medium"
        />
        {!isMini && (
          <Typography variant="h4" className="texto">
            /@LEANDROERCOLI
          </Typography>
        )}
      </div>
    </div>
  );
}
