import React, { useContext } from 'react';
import { Typography } from '@material-ui/core';
import { PortfolioContext } from '../../helpers'

export function Topbar({ setLanguage }) {
    const { language } = useContext(PortfolioContext);
    return (
        <div className="topbar">
            <Typography variant='body1' >LEANDRO ERCOLI</Typography>
            <div className="topbar-lang">
                <Typography variant='body2' className={language === "es" ? "lang-active" : ""} onClick={() => setLanguage("es")}>ES.</Typography>
                <Typography variant='body2' className={language === "en" ? "lang-active" : ""} onClick={() => setLanguage("en")} >EN.</Typography>
            </div>
        </div>
    )
}