import { useState, useEffect } from "react";

export const useLanguage = () => {
  const [language, setLanguage] = useState("es");

  useEffect(() => {
    var userLang = navigator.language || navigator.userLanguage;
    const language =
      userLang &&
      [
        "en",
        "en-US",
        "en-EG",
        "en-AU",
        "en-GB",
        "en-CA",
        "en-NZ",
        "en-IE",
        "en-ZA",
        "en-JM",
        "en-BZ",
        "en-TT",
      ].includes(userLang)
        ? "en"
        : "es";
    setLanguage(language);
  }, []);

  return [language, setLanguage];
};
