import React, { useContext } from 'react';
import { Grid } from './Layout'
import { PortfolioContext } from '../helpers'

export function ProjectsGrid() {
    const { language, labels } = useContext(PortfolioContext);
    const { header: { sectionTitles }, projects } = labels || {}

    return (
        <Grid sectionTitle={sectionTitles.projects[language]} gridElements={projects} gridCssClass="projects-container" />
    )
}