import React from "react";
import { createBrowserHistory } from "history";
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { ThemeProvider as MuiThemeProvider } from "@material-ui/core/styles";
import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
import { PortfolioContext, intialContext } from "./helpers";
import { useLanguage } from "hooks";
import Home from "containers/home";
import "./styles/global.scss";
import { ProjectsGrid } from "components";

const THEME = createMuiTheme({
  typography: {
    fontFamily: '"Jost", "Montserrat", "Dosis", sans-serif',
    fontSize: 14,
    fontWeightLight: 200,
    fontWeightRegular: 200,
    fontWeightMedium: 200,
  },
});

function App() {
  const history = createBrowserHistory();
  const [language, setLanguage] = useLanguage();

  return (
    <MuiThemeProvider theme={responsiveFontSizes(THEME)}>
      <PortfolioContext.Provider value={{ ...intialContext, language }}>
        <Router history={history}>
          <Switch>
            <Route path="/project/:id">
              <ProjectsGrid />
            </Route>
            <Route path="/">
              <Home setLanguage={setLanguage} />
            </Route>
            <Redirect from="*" to="/" />
          </Switch>
        </Router>
      </PortfolioContext.Provider>
    </MuiThemeProvider>
  );
}

export default App;
